<template>
  <div class="prescriptionRecord">
    <div class="filter">
      <el-date-picker
        v-model="dateRange"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        class="daterange item"
        :picker-options="pickerOptions">
      </el-date-picker>
      <el-button type="primary" class="searchBtn item" round>搜索</el-button>
      <el-button type="primary" class="searchBtn item" round>导出</el-button>
    </div>
    <el-table
      :data="linenList"
      style="width: 100%;"
      class="returnList"
      max-height="600"
    >
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-table :data="scope.row.goodsList">
            <el-table-column label="品名" width="500px">
              <template slot-scope="props">
                <p>{{props.row.name}}/{{props.row.num}}/{{props.row.size}}/{{props.row.company}}</p>
              </template>
            </el-table-column>
            <el-table-column label="批号/效期至">
              <template slot-scope="props">
                <p>{{props.row.batchNum}}/{{props.row.expiryDate}}</p>
              </template>
            </el-table-column>
            <el-table-column label="数量" prop="amount"></el-table-column>
          </el-table>
        </template>
      </el-table-column>
      <el-table-column label="销售时间" prop="time" width="160px"></el-table-column>
      <el-table-column label="药方单号" prop="orderNum"></el-table-column>
      <el-table-column label="顾客" prop="customer"></el-table-column>
      <el-table-column label="年龄" prop="age"></el-table-column>
      <el-table-column label="性别" prop="gender"></el-table-column>
      <el-table-column label="顾客身份证号" prop="idCard" width="160px"></el-table-column>
      <el-table-column label="联系方式" prop="phone" width="110px"></el-table-column>
      <el-table-column label="审方药师" prop="assessor" width="110px"></el-table-column>
      <el-table-column label="调配人" prop="deployer" width="110px"></el-table-column>
      <el-table-column label="复核人" prop="reviewer" width="110px"></el-table-column>
      <el-table-column label="药方剂数" prop="amount"></el-table-column>
    </el-table>
  </div>
</template>

<script>
  import {linenList, pickerOps} from '@/utils/staticData'
  import {secretData} from '@/utils/validate'
  export default {
    name: "prescriptionRecord",
    data() {
      return {
        dateRange: '',
        linenList: linenList,
        pickerOptions: pickerOps
      }
    },
    created() {
      this.$emit('changeDialogWidth', {dialogWidth: '95%'})
      this.linenList.forEach(item => {
        item.idCard = secretData(item.idCard, 'idCard')
      })
    }
  }
</script>

<style lang="scss" scoped>
  .prescriptionRecord{
    .filter {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 10px;

      .item {
        margin-right: 10px;
      }

      /deep/ {
        .el-autocomplete, .el-input {
          height: 40px;
          line-height: 40px;
          background-color: #ffffff;

          input {
            height: 100%;
            line-height: 100%;
            border-radius: 0;
          }
        }

        .el-range-input {
          width: 35%;
        }

        .el-range-separator {
          width: 16%;
        }

        .el-date-editor {
          width: 200px;
        }
      }

    }

    .returnList{
      .categories {
        /*line-height: 30px;*/
        p {
          /*font-size: 16px;*/
          width: 100%;
          font-weight: bold;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
</style>




