<template>
  <div class="receiptRecord">
    <div slot="title" class="dialog-title">
      <el-tabs v-model="activeTab" @tab-click="handleClick">
        <el-tab-pane label="登记信息" name="first"></el-tab-pane>
        <el-tab-pane label="处方单" name="second"></el-tab-pane>
      </el-tabs>
    </div>
    <div class="receiptContent">
      <div class="filter">
        <el-date-picker
          v-model="dateRange"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          class="daterange item"
          :picker-options="pickerOptions">
        </el-date-picker>
        <el-button type="primary" class="searchBtn item" round>搜索</el-button>
        <el-button type="primary" class="searchBtn item" round>导出</el-button>
      </div>
      <el-table
        :data="receiptList"
        style="width: 100%;"
        class="returnList"
        max-height="600"
      >
        <el-table-column label="销售时间" prop="time" width="160px"></el-table-column>
        <el-table-column label="药品" width="250px">
          <template slot-scope="scope">
            <div class="categories">
              <p>{{scope.row.name}}/{{scope.row.num}}</p>
              <i>{{scope.row.size}}/{{scope.row.company}}</i>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="批号/效期至" width="120px">
          <template slot-scope="scope">
            <i>{{scope.row.batchNum}}</i><br>
            <i>{{scope.row.usageDate}}</i>
          </template>
        </el-table-column>
        <el-table-column label="数量" prop="amount"></el-table-column>
        <el-table-column label="顾客" prop="customer"></el-table-column>
        <el-table-column label="年龄" prop="age"></el-table-column>
        <el-table-column label="性别" prop="gender"></el-table-column>
        <el-table-column label="顾客身份证号" prop="idCard" width="160px"></el-table-column>
        <el-table-column label="联系方式" prop="phone" width="110px"></el-table-column>
        <el-table-column label="审核员" prop="assessor"></el-table-column>
        <el-table-column label="登记员" prop="greffier"></el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import {receiptRegisterList, pickerOps} from '@/utils/staticData'
  import {secretData} from '@/utils/validate'

  export default {
    name: "receiptRecord",
    data() {
      return {
        activeTab: 'first',
        dateRange: '',
        receiptList: receiptRegisterList,
        pickerOptions: pickerOps
      }
    },
    created() {
      this.$emit('changeDialogWidth', {dialogWidth: '98%'})
      this.receiptList.forEach(item => {
        item.idCard = secretData(item.idCard, 'idCard')
      })
    },
    methods: {
      handleClick(tab) {
        //
      }
    }
  }
</script>

<style lang="scss" scoped>
  .receiptRecord {
    .dialog-title {

    }

    .receiptContent {
      .filter {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 10px;

        .item {
          margin-right: 10px;
        }

        /deep/ {
          .el-autocomplete, .el-input {
            height: 40px;
            line-height: 40px;
            background-color: #ffffff;

            input {
              height: 100%;
              line-height: 100%;
              border-radius: 0;
            }
          }

          .el-range-input {
            width: 35%;
          }

          .el-range-separator {
            width: 16%;
          }

          .el-date-editor {
            width: 200px;
          }
        }

      }

      .returnList{
        .categories {
          /*line-height: 30px;*/
          p {
            /*font-size: 16px;*/
            width: 100%;
            font-weight: bold;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
        }
      }

    }
  }
</style>



