<template>
  <div class="linenRegister">
    <p class="tip">购买含麻黄碱复方制剂身份登记，限购两盒</p>
    <el-form
      class="linenForm"
      ref="form"
      :model="form"
      :rules="formRules"
      label-width="12px"
    >
      <el-form-item prop="username" label=" ">
        <el-input
          v-model="form.username"
          name="username"
          placeholder="请输入姓名"
          type="text"
          tabindex="1"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="phone" label=" ">
        <el-input
          v-model="form.phone"
          type="text"
          tabindex="1"
          name="phone"
          placeholder="请输入手机号"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="idCard" label=" ">
        <el-input
          v-model="form.idCard"
          type="text"
          tabindex="1"
          name="idCard"
          placeholder="请输入身份证号"
          clearable
          autocomplete="off"
          @keyup.enter.native="handleSubmit"
        >
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          class="btn"
          @click.native.prevent="handleSubmit"
        >
          提交
        </el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
  import {identityCodeValid, identityPhone} from '@/utils/validate'

  export default {
    name: "linenRegister",
    data() {
      const validatePhone = (rule, value, callback) => {
        if (!identityPhone(value)) {
          callback(new Error('请输入正确的手机号码'))
        } else {
          callback()
        }
      }
      const validateIdCard = (rule, value, callback) => {
        let result = identityCodeValid(value)
        if (result.pass) {
          callback()
        } else {
          callback(new Error(result.tip))
        }
      }
      return {
        form: {
          username: '',
          phone: '',
          idCard: ''
        },
        loading: false,
        formRules: {
          username: [{required: true, trigger: 'blur',message: '请输入用户名'}],
          phone: [{required: true, trigger: 'blur', validator: validatePhone}],
          idCard: [{required: true, trigger: 'blur', validator: validateIdCard}]
        }
      }
    },
    created() {
      this.$emit('changeDialogWidth', {dialogWidth: '30%'})
    },
    methods: {
      handleSubmit() {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.loading = true
            setTimeout(() => {
              this.loading = false
            }, 1000)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  .linenRegister {
    .tip {
      color: #3a8ee6;
    }

    .linenForm {
      width: 300px;
      margin: 0 auto;
      margin-top: 30px;
      text-align: center;

      .btn {
        width: 120px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
      }
    }
  }
</style>



