<template>
  <div class="receiptRegister">
    <p class="tip">购买含麻黄碱复方制剂身份登记，限购两盒</p>
    <el-form
      class="receiptForm"
      ref="form"
      :model="form"
      :rules="formRules"
      label-width="12px"
    >
      <el-form-item prop="username" label=" ">
        <el-input
          v-model="form.username"
          name="username"
          placeholder="请输入姓名"
          type="text"
          tabindex="1"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="phone" label=" ">
        <el-input
          v-model="form.phone"
          type="text"
          tabindex="1"
          name="phone"
          placeholder="请输入手机号"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="idCard" label=" ">
        <el-input
          v-model="form.idCard"
          type="text"
          tabindex="1"
          name="idCard"
          placeholder="请输入身份证号"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item class="uploader">
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <i>添加处方</i>
      </el-form-item>
      <el-form-item prop="passName" label=" ">
        <el-input
          v-model="form.passName"
          name="username"
          placeholder="请输入药师手机或用户名"
          type="text"
          tabindex="1"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-tooltip v-model="capsTooltip" content="大写锁已打开" placement="right" manual>
        <el-form-item prop="password" label=" ">
          <el-input
            v-model="form.password"
            name="username"
            placeholder="请输入药师密码"
            tabindex="1"
            clearable
            autocomplete="off"
            show-password
            @keyup.native="checkCapslock"
            @blur="capsTooltip = false"
            @keyup.enter.native="handleSubmit"
          >
          </el-input>
        </el-form-item>
      </el-tooltip>

      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          class="btn"
          @click.native.prevent="handleSubmit"
        >
          提交
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {identityCodeValid, identityPhone} from '@/utils/validate'
  export default {
    name: "receiptRegister",
    data() {
      const validatePhone = (rule, value, callback) => {
        if (!identityPhone(value)) {
          callback(new Error('请输入正确的手机号码'))
        } else {
          callback()
        }
      }
      const validateIdCard = (rule, value, callback) => {
        let result = identityCodeValid(value)
        if (result.pass) {
          callback()
        } else {
          callback(new Error(result.tip))
        }
      }
      return {
        form: {
          username: '',
          phone: '',
          idCard: ''
        },
        loading: false,
        formRules: {
          username: [{required: true, trigger: 'blur',message: '请输入用户名'}],
          phone: [{required: true, trigger: 'blur', validator: validatePhone}],
          idCard: [{required: true, trigger: 'blur', validator: validateIdCard}],
          passName: [{required: true, trigger: 'blur',message: '该账号不存在'}],
          password: [{required: true, trigger: 'blur',message: '密码不能为空'}]
        },
        imageUrl: '',
        capsTooltip: false,
      }
    },
    created() {
      this.$emit('changeDialogWidth', {dialogWidth: '30%'})
    },
    methods: {
      handleSubmit() {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.loading = true
            setTimeout(() => {
              this.loading = false
            }, 1000)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        // const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        // if (!isJPG) {
        //   this.$message.error('上传头像图片只能是 JPG 格式!');
        // }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        // return isJPG && isLt2M;
        return isLt2M;
      },
      checkCapslock({ shiftKey, key } = {}) {
        if (key && key.length === 1) {
          if (shiftKey && (key >= 'a' && key <= 'z') || !shiftKey && (key >= 'A' && key <= 'Z')) {
            this.capsTooltip = true
          } else {
            this.capsTooltip = false
          }
        }
        if (key === 'CapsLock' && this.capsTooltip === true) {
          this.capsTooltip = false
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .receiptRegister {
    .tip {
      color: #3a8ee6;
    }
    .receiptForm{
      width: 300px;
      margin: 0 auto;
      margin-top: 30px;
      text-align: center;
      .uploader{
        text-align: left;
        .avatar-uploader{
          width: 50px;
          vertical-align: baseline;
          display: inline-block;
          text-align: left;
          border: 1px dashed #d9d9d9;
        }
        .avatar-uploader .el-upload {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 50px;
          height: 50px;
          line-height: 50px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }

      .btn {
        width: 120px;
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
      }
    }

  }
</style>



