<template>
  <div class="shortcutKey">
    <div class="usualKeys" :class="{marginRight: isExpand}">
      <div class="item" v-for="(item) in keys" :key="item.key" @click="quickKey(item.key, item.clipboard)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <div class="expendKeys" v-if="isExpand">
      <div class="item" v-for="(item) in anotherKeys" :key="item.key" @click="quickKey(item.key, item.clipboard)">
        <span>{{item.name}}</span>
        <span>【{{item.key}}】</span>
      </div>
    </div>
    <span class="tag" @click="changeExpand">
      <svg-icon :icon-class="isExpand ? 'packup' : 'expand'"></svg-icon>
    </span>

    <el-dialog
      modal-append-to-body
      append-to-body
      lock-scroll
      :before-close="handleClose"
      :width="dialogWidth"
      :visible.sync="dialogTableVisible"
    >
      <!-- 弹窗内容开始 搭配codeA和codeA的值选择展示哪种弹框 -->
      <!-- 优惠券 -->
      <coupons v-if="codeA && codeB === 122" @addTips="beforeCloseTips"></coupons>
      <!-- 提取挂单 -->
      <extract-receipts v-if="codeA && codeB === 114"></extract-receipts>
      <!-- 原单退货 -->
      <return-receipts v-if="codeA && codeB === 115" @changeDialogWidth="changeParentState"></return-receipts>
      <!-- 处方记录 -->
      <receipt-record v-if="codeA && codeB === 81" @changeDialogWidth="changeParentState"></receipt-record>
      <!-- 含麻记录 -->
      <linen-record v-if="codeA && codeB === 87" @changeDialogWidth="changeParentState"></linen-record>
      <!-- 药方记录 -->
      <prescription-record v-if="codeA && codeB === 69" @changeDialogWidth="changeParentState"></prescription-record>
      <!-- 含麻登记 -->
      <linen-register v-if="isLinen" @changeDialogWidth="changeParentState"></linen-register>
      <!-- 处方登记 -->
      <receipt-register v-if="isReceipt" @changeDialogWidth="changeParentState"></receipt-register>
      <!-- 办理会员 -->
      <apply-membership v-if="codeA && codeB === 116" @changeDialogWidth="changeParentState"></apply-membership>
      <!-- 重打小票 -->
      <prePrint-receipts v-if="codeA && codeB === 118" @changeDialogWidth="changeParentState"></prePrint-receipts>
      <!-- 弹窗内容结束 -->
    </el-dialog>
  </div>
</template>

<script>
  import coupons from "@/components/coupons"
  import extractReceipts from "@/components/extractReceipts"
  import returnReceipts from "@/components/returnReceipts"
  import receiptRecord from '@/components/receiptRecord'
  import linenRecord from '@/components/linenRecord'
  import prescriptionRecord from '@/components/prescriptionRecord'
  import linenRegister from '@/components/linenRegister'
  import receiptRegister from '@/components/receiptRegister'
  import applyMembership from '@/components/applyMembership'
  import prePrintReceipts from '@/components/prePrintReceipts'
  import {_KEYS, _ANOTHERKEYS} from "@/utils/staticData"

  export default {
    name: "shortcutKey",
    components: {
      coupons,
      extractReceipts,
      returnReceipts,
      receiptRecord,
      linenRecord,
      prescriptionRecord,
      linenRegister,
      receiptRegister,
      applyMembership,
      prePrintReceipts
    },
    data() {
      return {
        keys: _KEYS,
        anotherKeys: _ANOTHERKEYS,
        dialogWidth: '50%',
        closeText: '',
        codeB: 0,
        isExpand: false,
        dialogTableVisible: false,
        codeA: false,
        isCloseText: false,
        isLinen: false,
        isReceipt: false
      }
    },
    watch: {
      dialogTableVisible(val) {
        if (!val) {
          // 重置状态
          this.codeA = false
          this.codeB = 0
          this.isCloseText = false
          this.closeText = ''
          // 添加绑定事件
          window.addEventListener('keydown', this.focusKeydown, false)
          window.addEventListener('keyup', this.blurKeyup, false)
        }
      }
    },
    created() {
      window.addEventListener('keydown', this.focusKeydown, false)
      window.addEventListener('keyup', this.blurKeyup, false)
    },
    methods: {
      /**
       * 快捷键触发弹窗展示
       */
      quickKey(key, num) {
        this.codeA = true
        this.codeB = num
        this.changeDialogStatus()
      },
      changeExpand() {
        this.isExpand = !this.isExpand
      },
      changeDialogStatus() {
        this.dialogTableVisible = !this.dialogTableVisible
        if (this.dialogTableVisible) {
          // 解绑事件
          window.removeEventListener('keydown', this.focusKeydown, false)
          window.removeEventListener('keyup', this.blurKeyup, false)
        }
      },
      focusKeydown(e) {
        console.log('codeA:', this.codeA, 'codeB:', this.codeB);
        let key = window.event.keyCode;

        if (key === 18) {
          this.codeA = true
        } else {
          this.codeB = key
        }
        if (this.codeA && _KEYS.some(item => item.clipboard === this.codeB)) {
          e.cancelBubble = true;
          e.returnValue = false;
          if (e.preventDefault) e.preventDefault();
          if (e.stopPropagation) e.stopPropagation();
          this.dialogTableVisible = true
        }
      },
      blurKeyup(e) {
        let key = window.event.keyCode;
        console.log(key, 'key')
        if (this.dialogTableVisible) {
          // 解绑事件
          window.removeEventListener('keydown', this.focusKeydown, false)
          window.removeEventListener('keyup', this.blurKeyup, false)
        } else {
          if (key === 18) {
            this.codeA = false
          } else {
            this.codeB = 0
          }
        }
      },
      handleClose(done) {
        console.log(this.isCloseText, 'this.isCloseText');
        if (this.isCloseText) {
          this.$confirm(this.closeText)
            .then(_ => {
              done()
            })
            .catch(_ => {
              return false
            })
        } else {
          done()
        }

      },
      beforeCloseTips(tips) {
        this.isCloseText = true
        this.closeText = tips
      },
      changeParentState(obj) {
        Object.assign(this, obj)
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "~@/style/common/variables.scss";

  .shortcutKey {
    display: flex;
    /*padding: 0 10px;*/
    position: relative;
    background-color: #ffffff;

    span {
      display: block;
    }

    .usualKeys, .expendKeys {
      display: flex;
      flex-wrap: wrap;
      max-width: 580px;

      .item {
        cursor: pointer;
        color: $menuText;
        width: 115px;
        padding: 0 15px;
        text-align: center;
        line-height: 30px;
        max-height: 60px;

        &:hover {
          border-radius: 10px;
          background-color: $menuActiveText;
          color: #ffffff;
        }
      }
    }

    .usualKeys {
      &.marginRight {
        margin-right: 10px;
      }
    }

    .expendKeys {
      flex-direction: column;
      max-height: 180px;
      flex-wrap: wrap;
    }

    .tag {
      -moz-user-select: none;
      -webkit-user-select: none;
      user-select: none;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      padding: 6px 5px 6px 15px;
    }
  }
</style>



