<template>
  <div class="applyMembership">
    <el-form
      class="applyForm"
      ref="form"
      :model="form"
      :rules="formRules"
      label-width="12px"
    >
      <el-form-item prop="username" label=" ">
        <el-input
          v-model="form.username"
          name="username"
          placeholder="请输入办卡人姓名"
          type="text"
          tabindex="1"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="phone" label=" ">
        <el-input
          v-model="form.phone"
          type="text"
          tabindex="1"
          name="phone"
          placeholder="请输入办卡人手机号"
          clearable
          autocomplete="off"
        >
        </el-input>
      </el-form-item>
      <el-form-item prop="idCard">
        <el-input
          v-model="form.idCard"
          type="text"
          tabindex="1"
          name="idCard"
          placeholder="请输入身份证号"
          clearable
          autocomplete="off"
          @blur="watchIdCard"
        >
        </el-input>
      </el-form-item>
      <el-form-item class="gender">
        <el-radio-group v-model="gender" disabled>
          <el-radio :label="0">男</el-radio>
          <el-radio :label="1">女</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item>
        <el-date-picker
          class="datePicker"
          v-model="bornDate"
          type="date"
          tabindex="1"
          disabled
          placeholder="请选择出生日期"
          value-format="yyyy-MM-dd"
          format="yyyy 年 MM 月 dd 日"
          autocomplete="off"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-input
          v-model="form.address"
          type="text"
          tabindex="1"
          placeholder="请输入办卡人联系地址"
          autocomplete="off"
          @keyup.enter.native="handleSubmit"
        >
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          :loading="loading"
          type="primary"
          class="btn"
          @click.native.prevent="handleSubmit"
        >
          提交
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {identityCodeValid, identityPhone} from '@/utils/validate'
  export default {
    name: "applyMembership",
    data() {
      const validatePhone = (rule, value, callback) => {
        if (!identityPhone(value)) {
          callback(new Error('请输入正确的手机号码'))
        } else {
          callback()
        }
      }
      const validateIdCard = (rule, value, callback) => {
        let result = identityCodeValid(value)
        if (result.pass) {
          callback()
        } else {
          callback(new Error(result.tip))
        }
      }
      return {
        form: {
          username: '',
          phone: '',
          idCard: '',
          address: ''
        },
        loading: false,
        bornDate: '',
        gender: '',
        formRules: {
          username: [{required: true, trigger: 'blur',message: '请输入用户名'}],
          phone: [{required: true, trigger: 'blur', validator: validatePhone}],
          idCard: [{required: false, trigger: 'blur', validator: validateIdCard}]
        }
      }
    },
    created() {
      this.$emit('changeDialogWidth', {dialogWidth: '30%'})
    },
    methods: {
      handleSubmit() {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.loading = true
            setTimeout(() => {
              this.loading = false
            }, 1000)
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      watchIdCard(){
        let result = identityCodeValid(this.form.idCard)
        if (result.pass) {
          this.bornDate = result.more.birthDate
          this.gender = result.more.sex
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .applyMembership {
    .applyForm{
      width: 300px;
      margin: 0 auto;
      margin-top: 30px;
      text-align: center;

      .btn {
        /*width: 120px;*/
        -webkit-border-radius: 5px;
        -moz-border-radius: 5px;
        border-radius: 5px;
      }
      .gender{
        text-align: left;
      }
      .datePicker{
        width: 100%;
      }
    }
  }
</style>



